.Golf-text--wrapper{gap:2rem;padding:2rem 2.5rem}@media screen and (min-width: 990px){.Golf-text--wrapper{padding:4rem}}@media screen and (max-width: 600px){.Golf-text--wrapper{flex-direction:column;gap:1rem}}.Golf-text--wrapper.flex-column{gap:0}.Golf-text--wrapper>*{flex:0 1 50%}.Golf-text__heading{width:100%;text-align:var(--align-desktop)}@media screen and (max-width: 600px){.Golf-text__heading{text-align:var(--align-mobile)}}.Golf-text__text{width:100%;text-align:var(--align-desktop)}@media screen and (max-width: 600px){.Golf-text__text{text-align:var(--align-mobile)}}.Golf-text__text--big{text-transform:uppercase;font-family:"GalanoGrotesqueMedium";font-size:clamp(1.2rem,3vw - 1rem,1.8rem)}